import React, {useEffect, useState} from 'react';
import {KEYS_DESCRIPTIONS} from "../../js/refs";
import Org from "./Org";
import {MANAGEMENT, MESSAGES} from "../../js/messages";
import ButtonNeutral from "../../components/UI/buttons/ButtonNeutral";
import useModal from "../../components/hooks/useModal";
import Modal from "../../components/UI/Modal/Modal";

const OrgsList = (props) => {
    const {
        orgs,
        canEdit,
        header
    } = props;
    const MODAL_EYE = useModal(null);
    //  startYear  endYear  ,     agreementStartDate  agreementEndDate
    const LEGEND = Object.keys(orgs[0]).
        filter(key => key !== 'orgId').
        filter(key => key !== 'startYear' && key !== 'endYear').
        map(key => KEYS_DESCRIPTIONS[key]);
    return (
        <div>
            <table>
                <caption>{header}</caption>
                <thead>
                    <tr>
                        {LEGEND.map(key => <td
                            key={key}
                        >{key}</td>)}
                        {canEdit &&
                            <td>{MESSAGES.ACTIONS}</td>
                        }
                    </tr>
                </thead>
                <tbody>
                    {orgs.map(org => {
                        return <Org
                            key={org.orgId}
                            orgInfo={org}
                            canEdit={canEdit}
                            MODAL_EYE={MODAL_EYE}
                        />
                    })}
                </tbody>
            </table>
            <Modal
                visible={MODAL_EYE.isVisible}
                setVisible={MODAL_EYE.setIsVisible}
            >{MODAL_EYE.CONTENT}</Modal>
        </div>
    );
};

export default OrgsList;