import React, {useContext, useState} from 'react';
import {MANAGEMENT, MESSAGES} from "../../js/messages";
import {AppContext} from "../../components/context";
import MServerDataLoader from "./MServerDataLoader";
import MyFileReader from "../../components/MyFileReader";
import ButtonSuccess from "../../components/UI/buttons/ButtonSuccess";
import ButtonRemove from "../../components/UI/buttons/ButtonRemove";
import {useNavigate} from "react-router-dom";

const ManagementMain = () => {
    const [fileContent, setFileContent] = useState(null);
    const navigate = useNavigate();
    const {
        serverData,
        setOfflineData
    } = useContext(AppContext);
    const clearFileContent = () => {
        setFileContent(null);
    }
    const next = () => {
        setOfflineData(fileContent);
        navigate('/management/orgs');
    }
    return (
        <div>
            <h1>{MANAGEMENT.MNG}</h1>
            <MServerDataLoader/>
            {serverData !== null
                ? serverData?.orgs
                    ? <div>
                        <ButtonSuccess
                            onClick={() => navigate('/management/orgs')}
                        ></ButtonSuccess>
                    </div>
                    : <div>
                    <span className="warning">
                        {MANAGEMENT.NO_ORGS_ON_SERVER}
                    </span>
                    </div>
                : ''
            }
            <h2>{MANAGEMENT.OR}</h2>
            <div>
                <MyFileReader
                    setFileContent={setFileContent}
                />
            </div>
            {fileContent !== null &&
                <div>
                    <ButtonRemove
                        onClick={clearFileContent}
                    >{MESSAGES.CANCEL}</ButtonRemove>
                    <ButtonSuccess
                        onClick={next}
                    >{MESSAGES.NEXT}</ButtonSuccess>
                </div>
            }
        </div>
    );
};

export default ManagementMain;