import {hashSHA256, X} from "./hashSHA256";

export const readData = async () => {
    try {
        const req = await fetch("https://api.jsonbin.io/v3/b/6768fce7ad19ca34f8df8b40/latest", {
            headers: {
                "accept": "*/*",
                "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
                "priority": "u=1, i",
                "X-Access-Key": '$2a$10$.0.WSLREdWCRCqdjrtzreuWJS3FBnsqy0XMKJmXGUYSPQFXlhiIRq',
            },
            body: null,
            method: "GET",
            mode: "cors",
            credentials: "omit",
        });
        if (!req.ok) {
            throw new Error(` HTTP: ${req.status} ${req.statusText}`);
        }
        const data = await req.json();
        return data.record;
    } catch (error) {
        return null;
    }
};

export const saveData = async newData => {
    try {
        const req = await fetch("https://api.jsonbin.io/v3/b/6768fce7ad19ca34f8df8b40/", {
            "headers": {
                "Content-Type": "application/json",
                "accept": "*/*",
                "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
                "priority": "u=1, i",
                "X-Access-Key": '$2a$10$.0.WSLREdWCRCqdjrtzreuWJS3FBnsqy0XMKJmXGUYSPQFXlhiIRq'
            },
            "body": newData,
            "method": "PUT",
            "mode": "cors",
            "credentials": "omit"
        });
        if (!req.ok) {
            throw new Error(` HTTP: ${req.status} ${req.statusText}`);
        }
        return req.ok;
    } catch (error) {
        return null;
    }
};

export const checkHash = async t => {
    const V = await hashSHA256(t);
    return V === X;
};

//       GitHub
const updateGitHubFile = async () => {
    const token = process.env.REACT_APP_API_KEY;
    console.log(token);
    const username = 'nikoreon5';
    const repo = 'UsuePractice';
    const filePath = 'orgs.json';
    const newContent = '[{x: y}]';
    const commitMessage = '   API';
    try {
        const shaResponse = await fetch(`https://api.github.com/repos/${username}/${repo}/contents/${filePath}`, {
            method: 'GET',
            headers: {
                Authorization: `Bearer ${token}`,
            },
        });
        if (!shaResponse.ok) {
            throw new Error(`   SHA: ${shaResponse.status} ${shaResponse.statusText}`);
        }
        const shaData = await shaResponse.json();
        const currentSHA = shaData.sha;
        const encodedContent = btoa(newContent);
        const updateResponse = await fetch(`https://api.github.com/repos/${username}/${repo}/contents/${filePath}`, {
            method: 'PUT',
            headers: {
                Authorization: `Bearer ${token}`,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                message: commitMessage,
                content: encodedContent,
                sha: currentSHA,
            }),
        });
        if (!updateResponse.ok) {
            throw new Error(`E: ${updateResponse.status} ${updateResponse.statusText}`);
        }
        const updateData = await updateResponse.json();
    } catch (error) {
        console.error('E:', error.message);
    }
};

