import React from 'react';
import {MANAGEMENT, MESSAGES} from "../../js/messages";
import DefInput from "../../components/UI/input/DefInput";
import ButtonSuccess from "../../components/UI/buttons/ButtonSuccess";
import Modal from "../../components/UI/Modal/Modal";

const ManagementLogin = (props) => {
    const {
        y,
        setY,
        m,
        checkPW
    } = props;
    return (
        <div>
            <h1>{MESSAGES.ADMIN}</h1>
            <DefInput
                value={y}
                onChange={e => setY(e.target.value)}
                placeholder={MANAGEMENT.ENTER_PASSWORD}
                type="password"
            />
            <div>
                <ButtonSuccess
                    disabled={!y.trim().length}
                    onClick={checkPW}
                >
                    {MANAGEMENT.LOGIN}
                </ButtonSuccess>
            </div>
            <Modal visible={m.isVisible} setVisible={m.setIsVisible}>
                {m.CONTENT}
            </Modal>
        </div>
    );
};

export default ManagementLogin;