import React, {useContext, useEffect, useState} from 'react';
import {MANAGEMENT, MESSAGES} from "../../js/messages";
import ManagementMain from "./ManagementMain";
import {checkHash} from "../../js/utils";
import useModal from "../../components/hooks/useModal";
import ButtonRemove from "../../components/UI/buttons/ButtonRemove";
import {AppContext} from "../../components/context";
import GoBack from "../../components/GoBack";
import ManagementLogin from "./ManagementLogin";

const Management = () => {
    const [y, setY] = useState('');
    const {
        isAuth,
        setIsAuth
    } = useContext(AppContext);
    const CM = () => {
        m.hideModal();
        setY('');
    }
    const m = useModal(
        <div>
            <h3 className="err">{MANAGEMENT.INCORRECT_PASSWORD}</h3>
            <ButtonRemove
                onClick={CM}
            >{MESSAGES.CLOSE}</ButtonRemove>
        </div>
    );
    const checkPW = async () => {
        const T = await checkHash(y);
        setIsAuth(T);
        if (!T) {
            m.showModal();
        }
    }
    useEffect(() => {
        if (!m.isVisible) {
            setY('');
        }
    }, [m.isVisible]);
    return (
        <div>
            <div className="centered-div">
                {isAuth
                    ? <ManagementMain/>
                    : <ManagementLogin
                        y={y}
                        setY={setY}
                        m={m}
                        checkPW={checkPW}
                    />
                }
            </div>
            <GoBack/>
        </div>
    );
};

export default Management;