import React, {useId, useState} from "react";
import {MANAGEMENT, MESSAGES} from "../js/messages";
import DefInput from "./UI/input/DefInput";
import Modal from "./UI/Modal/Modal";
import useModal from "./hooks/useModal";
import ButtonWarning from "./UI/buttons/ButtonWarning";

const MyFileReader = (props) => {
    const [fileName, setFileName] = useState(MANAGEMENT.FILE_NOT_CHOSEN);
    const id = useId();
    const {
        fileContent,
        setFileContent
    } = props;
    const readFile = event => {
        const file = event.target.files[0];
        setFileName(file ? file.name : MANAGEMENT.FILE_NOT_CHOSEN);
        if (file) {
            const reader = new FileReader();
            reader.onload = (e) => {
                let result = e.target.result;
                try {
                    result = JSON.parse(result);
                } catch {
                    parseErrModal.showModal();
                    return;
                }
                setFileContent(result)
            };
            reader.readAsText(file);
        }
    };
    const errCloseModal = () => {
        setFileName(MANAGEMENT.FILE_NOT_CHOSEN);
        parseErrModal.hideModal();
    }
    const parseErrModal = useModal(
        <div>
            <span className="err">{MANAGEMENT.ERR_READ_FILE}</span>
            <ButtonWarning
                onClick={errCloseModal}
            >{MESSAGES.CLOSE}</ButtonWarning>
        </div>
    )
    return (
        <div className="file-input-container">
            <Modal
                visible={parseErrModal.isVisible}
                setVisible={parseErrModal.setIsVisible}
            >
                {parseErrModal.CONTENT}
            </Modal>
            <DefInput
                type="file"
                id={id}
                onChange={readFile}
                style={{display: "none"}}
            />
            <label htmlFor={id} className="custom-file-label">
                {MANAGEMENT.LOAD_FILE_FROM_PC}
            </label>
            <p className="custom-file-name">{fileName}</p>
        </div>
    );
}

export default MyFileReader;