import React, {useEffect} from 'react';
import ButtonPrimary from "../../components/UI/buttons/ButtonPrimary";
import {MESSAGES} from "../../js/messages";
import ButtonNeutral from "../../components/UI/buttons/ButtonNeutral";
import useModal from "../../components/hooks/useModal";
import {KEYS_DESCRIPTIONS} from "../../js/refs";
import Modal from "../../components/UI/Modal/Modal";

const Org = (props) => {
    const {
        orgInfo,
        canEdit,
        MODAL_EYE
    } = props;
    const ADMISSION_DIRECTIONS_CONTAINER = <div>
        <h2>{KEYS_DESCRIPTIONS.admissionDirections}</h2>
        <span className="info">
            {orgInfo.admissionDirections}
        </span>
        <div>
            <ButtonNeutral
                onClick={() => MODAL_EYE.hideModal()}
            >
                <span className="glyphicon glyphicon-check"></span>
            </ButtonNeutral>
        </div>
    </div>
    const showAdmissionDirections = () => {
        MODAL_EYE.setCONTENT(ADMISSION_DIRECTIONS_CONTAINER);
        MODAL_EYE.showModal();
    }
    const SHORT_ORG_NAME = orgInfo.orgName.split(',')[0];
    return (
        <tr>
            {/*<td>{orgInfo.startYear}</td>*/}
            {/*<td>{orgInfo.endYear}</td>*/}
            <td>{SHORT_ORG_NAME}</td>
            <td>{orgInfo.agreementStartDate}</td>
            <td>{orgInfo.agreementEndDate}</td>
            <td>{orgInfo.longTermCooperationAgreementNumber}</td>
            <td>{orgInfo.cooperationAgreement}</td>
            <td>{orgInfo.practicalTrainingAgreementNumber}</td>
            <td>{orgInfo.practicalTrainingContract}</td>
            {/*<td>{orgInfo.admissionDirections}</td>*/}
            <td>
                <ButtonNeutral
                    onClick={showAdmissionDirections}
                >
                    <span className="glyphicon glyphicon-eye-open"></span>
                </ButtonNeutral>
            </td>
            <td>{orgInfo.isHidden}</td>
            {canEdit &&
                <td>
                    <ButtonNeutral>
                        <span className="glyphicon glyphicon-pencil"></span>
                    </ButtonNeutral>
                </td>
            }
        </tr>
    );
};

export default Org;