import React, {useContext, useState} from 'react';
import GoBack from "../../components/GoBack";
import {AppContext} from "../../components/context";
import {MANAGEMENT, MESSAGES} from "../../js/messages";
import OrgsList from "./OrgsList";
import {saveData} from "../../js/utils";
import ButtonSuccess from "../../components/UI/buttons/ButtonSuccess";

const ManagementOrgs = () => {
    const {
        isAuth,
        serverData,
        setServerData,
        offlineData
    } = useContext(AppContext);
    const [orgsSaved, setOrgsSaved] = useState(!offlineData);
    const [orgsStatus, setOrgsStatus] = useState(orgsSaved
        ? ''
        : <span className="warning">{MANAGEMENT.ORGS_NOT_SENT}</span>
    );
    const saveOrgs = async () => {
        setOrgsStatus(
            <span className="info">{MANAGEMENT.SAVING_ORGS}</span>
        );
        let data;
        if (offlineData !== null) {
            console.log('offline mode')
            data = {
                orgs: offlineData
            };
        } else if (serverData !== null) {
            data = {
                ...serverData,
                orgs: serverData.orgs
            };
        }
        const saved = await saveData(JSON.stringify(data));
        console.log('data', saved);
        if (saved === null) {
            setOrgsStatus(
                <span className="danger">
                    {MANAGEMENT.AN_ERROR_OCCURED}
                </span>
            );
            return;
        }
        setOrgsStatus(<span className="success">{MANAGEMENT.DATA_SAVED}</span>);
        setServerData(offlineData);
    }
    console.log(offlineData);
    return (
        <div>
            <div className="centered-div" style={{maxWidth: '100%'}}>
                {isAuth
                    ? <div>
                        <h1>{MANAGEMENT.ORGS}</h1>
                        {orgsStatus &&
                            <div className="container">
                                {orgsStatus}
                            </div>
                        }
                        <div>
                            <ButtonSuccess
                                onClick={saveOrgs}
                            >{MESSAGES.SEND}</ButtonSuccess>
                        </div>
                        {serverData?.orgs &&
                            <OrgsList
                                orgs={serverData.orgs}
                                header={MANAGEMENT.ORGS_LIST}
                                canEdit={true}
                            />
                        }
                        {offlineData &&
                            <OrgsList
                                orgs={offlineData}
                                header={MANAGEMENT.ORGS_LIST}
                                canEdit={true}
                            />
                        }
                    </div>
                    : <div>
                        <span className="err">{MANAGEMENT.ACCESS_DENIED}</span>
                    </div>
                }
            </div>
            <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                <GoBack/>
            </div>
        </div>
    );
};

export default ManagementOrgs;