import React, {useContext, useState} from 'react';
import ButtonPrimary from "../../components/UI/buttons/ButtonPrimary";
import {MANAGEMENT} from "../../js/messages";
import {readData} from "../../js/utils";
import {AppContext} from "../../components/context";

const MServerDataLoader = () => {
    const [btnLoadOrgsDisabled, setBtnLoadOrgsDisabled] = useState(false);
    const [status, setStatus] = useState(
        <span className="primary">{MANAGEMENT.CLICK_THE_BUTTON_TO_LOAD_ORGS}</span>
    );
    const {
        serverData,
        setServerData
    } = useContext(AppContext);
    const loadData = async () => {
        setBtnLoadOrgsDisabled(true);
        setStatus(
            <span className="info">{MANAGEMENT.READING_DATA}</span>
        )
        const data = await readData();
        if (data === null) {
            setStatus(
                <span className="danger">
                    {MANAGEMENT.AN_ERROR_OCCURED}
                </span>
            )
            setBtnLoadOrgsDisabled(false);
            return;
        }
        setServerData(data.record);
        setBtnLoadOrgsDisabled(false);
        setStatus(<span className="success">{MANAGEMENT.DATA_LOADED}</span>)
    }
    return (
        <div>
            <ButtonPrimary
                disabled={btnLoadOrgsDisabled}
                onClick={loadData}
            >{MANAGEMENT.LOAD_ORGS}</ButtonPrimary>
            <div>
                {status}
            </div>
        </div>
    );
};

export default MServerDataLoader;